import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"

export default function AiAssistantCapabilities() {
  const capabilities = [
    {
      id: "symptom-analysis",
      title: "Symptom Analysis",
      description: "Our AI can analyze your symptoms and provide preliminary insights based on medical knowledge.",
      features: [
        "Comprehensive symptom assessment",
        "Follow-up questions for clarity",
        "Potential condition suggestions",
        "Severity assessment",
        "Personalized recommendations",
        "Medical referrals when necessary",
      ],
      icon: (
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="24"
          height="24"
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          strokeWidth="2"
          strokeLinecap="round"
          strokeLinejoin="round"
          className="text-teal-600"
        >
          <path d="M2 9a3 3 0 0 1 0 6v2a2 2 0 0 0 2 2h16a2 2 0 0 0 2-2v-2a3 3 0 0 1 0-6V7a2 2 0 0 0-2-2H4a2 2 0 0 0-2 2Z"></path>
          <path d="M9 12h6"></path>
          <path d="M12 9v6"></path>
        </svg>
      ),
    },
    {
      id: "health-education",
      title: "Health Education",
      description:
        "Access a vast library of health information, medical conditions, treatments, and preventive measures.",
      features: [
        "Evidence-based health information",
        "Condition explanations in simple language",
        "Treatment options overview",
        "Medication information",
        "Preventive health measures",
        "Health tips and best practices",
      ],
      icon: (
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="24"
          height="24"
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          strokeWidth="2"
          strokeLinecap="round"
          strokeLinejoin="round"
          className="text-teal-600"
        >
          <path d="M4 19.5v-15A2.5 2.5 0 0 1 6.5 2H20v20H6.5a2.5 2.5 0 0 1 0-5H20"></path>
        </svg>
      ),
    },
    {
      id: "wellness-coaching",
      title: "Wellness Coaching",
      description: "Receive personalized guidance for improving your overall health and wellness.",
      features: [
        "Nutrition advice and meal planning",
        "Exercise recommendations",
        "Sleep improvement strategies",
        "Stress management techniques",
        "Mental wellness support",
        "Habit formation guidance",
      ],
      icon: (
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="24"
          height="24"
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          strokeWidth="2"
          strokeLinecap="round"
          strokeLinejoin="round"
          className="text-teal-600"
        >
          <path d="M16 21v-2a4 4 0 0 0-4-4H6a4 4 0 0 0-4 4v2"></path>
          <circle cx="9" cy="7" r="4"></circle>
          <path d="M22 21v-2a4 4 0 0 0-3-3.87"></path>
          <path d="M16 3.13a4 4 0 0 1 0 7.75"></path>
        </svg>
      ),
    },
    {
      id: "service-integration",
      title: "Service Integration",
      description: "Seamlessly connect with other ONE HEALTHLINE CONNECT services based on your needs.",
      features: [
        "Appointment scheduling",
        "Teleconsultation initiation",
        "Medication ordering",
        "Emergency service access",
        "Health record integration",
        "Follow-up reminders",
      ],
      icon: (
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="24"
          height="24"
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          strokeWidth="2"
          strokeLinecap="round"
          strokeLinejoin="round"
          className="text-teal-600"
        >
          <rect width="18" height="18" x="3" y="3" rx="2" ry="2"></rect>
          <line x1="3" x2="21" y1="9" y2="9"></line>
          <line x1="9" x2="9" y1="21" y2="9"></line>
        </svg>
      ),
    },
  ]

  return (
    <section className="py-16 bg-white dark:bg-gray-900 transition-colors duration-300">
      <div className="container mx-auto px-4">
        <div className="text-center mb-12">
          <h2 className="text-3xl font-bold text-gray-900 dark:text-white mb-4">
            AI Assistant Capabilities
          </h2>
          <p className="text-xl text-gray-600 dark:text-gray-300 max-w-3xl mx-auto">
            Explore the powerful capabilities of our AI Health Assistant designed to support your health journey.
          </p>
        </div>

        <Tabs defaultValue="symptom-analysis" className="w-full">
          <TabsList className="grid grid-cols-2 md:grid-cols-4 gap-2 bg-gray-100 dark:bg-gray-800 p-1 rounded-lg mb-8">
            {capabilities.map((capability) => (
              <TabsTrigger
                key={capability.id}
                value={capability.id}
                className="data-[state=active]:bg-teal-600 data-[state=active]:text-white"
              >
                <div className="flex items-center gap-2">
                  <span className="hidden md:inline-block">{capability.icon}</span>
                  <span>{capability.title}</span>
                </div>
              </TabsTrigger>
            ))}
          </TabsList>

          {capabilities.map((capability) => (
            <TabsContent key={capability.id} value={capability.id} className="mt-0">
              <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 items-center bg-gray-50 dark:bg-gray-800 rounded-lg p-8">
                <div>
                  <div className="flex items-center gap-3 mb-4">
                    {capability.icon}
                    <h3 className="text-2xl font-bold text-gray-900 dark:text-white">{capability.title}</h3>
                  </div>
                  <p className="text-lg text-gray-700 dark:text-gray-300 mb-6">{capability.description}</p>
                  <ul className="space-y-3">
                    {capability.features.map((feature, index) => (
                      <li key={index} className="flex items-start gap-2">
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="20"
                          height="20"
                          viewBox="0 0 24 24"
                          fill="none"
                          stroke="currentColor"
                          strokeWidth="2"
                          strokeLinecap="round"
                          strokeLinejoin="round"
                          className="text-teal-600 mt-1"
                        >
                          <path d="M12 22c5.523 0 10-4.477 10-10S17.523 2 12 2 2 6.477 2 12s4.477 10 10 10z"></path>
                          <path d="m9 12 2 2 4-4"></path>
                        </svg>
                        <span className="text-gray-700 dark:text-gray-300">{feature}</span>
                      </li>
                    ))}
                  </ul>
                </div>
                <div className="bg-white dark:bg-gray-700 p-6 rounded-xl shadow-md">
                  {/* You can keep your conditional capability content here and just add dark: classes */}
                  {/* Example for text */}
                  <p className="text-gray-700 dark:text-gray-200">
                    This content will respect dark mode.
                  </p>
                </div>
              </div>
            </TabsContent>
          ))}
        </Tabs>
      </div>
    </section>
  )
}
